































import canvasAnimation from './canvas/animation';
import Header from "@/components/Header.vue";
import HeaderBack from "@/components/HeaderBack.vue";
import CloudNative from "@/components/CloudNative.vue";
import { Component, Vue } from "vue-property-decorator";

@Component({
    components: { Header, HeaderBack, CloudNative }
})
export default class Home extends Vue {
    isScroll = false;

    active = 0;

    mounted () {
        let canvas = document.querySelector("#canvas") as HTMLCanvasElement;
        canvasAnimation(canvas);
    }

    scroll(e: Event) {
        const page = document.querySelector("#page");
        if (page === null) {
            return;
        }
        let navContents = document.querySelectorAll('.header-back');
        let offsetTopArr: number[] = [];
        navContents.forEach((item) => {
            let one = item as HTMLElement;
            offsetTopArr.push(one.offsetTop)
        });

        const scrollTop = page.scrollTop;
        let navIndex = 0;

        for (let n = 0; n < offsetTopArr.length; n++ ) {
            if (scrollTop >= offsetTopArr[n]) {
                navIndex = n
            }
        }
        this.active = navIndex;

        this.isScroll = scrollTop > 76;
    }

    scrollTo (index) {
        let navContents = document.querySelectorAll('.header-back');
        let offsetTopArr: number[] = [];
        navContents.forEach((item) => {
            let one = item as HTMLElement;
            offsetTopArr.push(one.offsetTop)
        });

        const page = document.querySelector("#page");
        if (page === null) return;
        const targetOffsetTop = offsetTopArr[index];
        let scrollTop = page.scrollTop;
        const STEP = 50;
        if (scrollTop > targetOffsetTop) {
            smoothUp()
        } else {
            smoothDown()
        }

        function smoothDown() {
            if (page === null) return;
            // 如果当前 scrollTop 小于 targetOffsetTop 说明视口还没滑到指定位置
            if (scrollTop < targetOffsetTop) {
                // 如果和目标相差距离大于等于 STEP 就跳 STEP
                // 否则直接跳到目标点，目标是为了防止跳过了。
                if (targetOffsetTop - scrollTop >= STEP) {
                    scrollTop += STEP
                } else {
                    scrollTop = targetOffsetTop
                }
                page.scrollTop = scrollTop;
                // 关于 requestAnimationFrame 可以自己查一下，在这种场景下，相比 setInterval 性价比更高
                requestAnimationFrame(smoothDown)
            }
        }
        // 定义往上滑函数
        function smoothUp() {
            if (page === null) return;
            if (scrollTop > targetOffsetTop) {
                if (scrollTop - targetOffsetTop >= STEP) {
                    scrollTop -= STEP
                } else {
                    scrollTop = targetOffsetTop
                }
                page.scrollTop = scrollTop;
                requestAnimationFrame(smoothUp)
            }
        }
    }

}
